  transform: rotate(-135deg); }

.filter__button button {
  width: 100%;
  min-height: 68px;
  font-family: "Poppins", "Arial", sans-serif;
  font-weight: 500;
  font-size: 16px;
  line-height: 21px;
  border: 0;
  color: #ffffff;
  cursor: pointer; }

.filter__button--apply button {
  margin-top: 32px;
  margin-bottom: 12px;
  background-color: #a87b62; }

.filter__button--clear button {
  margin-top: 13px;
  margin-bottom: 25px;
  color: #1d1613;
  background-color: #ffffff;
  border: 1px solid #a87b62; }

.filter__button--close button {
  display: none; }

.filter__button--open {
  display: none; }

@media (max-width: 1023px) {
  .filter__button--open {
    display: block;
    width: 100%;
    margin-bottom: 50px;
    min-height: 68px;
    font-family: "Poppins", "Arial", sans-serif;
    font-weight: 500;
    font-size: 16px;
    line-height: 21px;
    color: #ffffff;
    background-color: #a87b62;
    border: 0;
    border-radius: 0; } }

@media (max-width: 767px) {
  .filter__button--open {
    margin-bottom: 40px; } }

.price__wrapper {
  display: none;
  min-height: 58px; }

.price__all-price-range {
  height: 1px;
  width: 200px;
  background-color: #f0eae4;
  position: absolute;
  top: 70px; }

.price__actually-price-range {
  height: 1px;
  width: 90px;
  background-color: #a87b62;
  position: absolute;
  top: 70px;
  left: 84px; }

.price__toggler {
  width: 15px;
  height: 15px;
  border-radius: 50%;
  background-color: #a87b62;
  position: absolute;
  top: 63px;
  cursor: pointer; }
  .price__toggler--min {
    left: 69px; }
  .price__toggler--max {
    left: 174px; }
  .price__toggler:hover, .price__toggler:focus {
    background-color: #a87b62; }

.price__inputs {
  margin-top: 21px;
  margin-bottom: 19px;
  position: relative; }

.price__input {
  display: flex;
  position: absolute; }
  .price__input:first-child {
    margin-right: 20px; }
  .price__input label {
    padding: 0;
    font-family: "Poppins", "Arial", sans-serif;
    font-size: 14px;
    line-height: 18px;
    color: #1d1613; }
    .price__input label:hover {
      color: rgba(29, 22, 19, 0.7); }
  .price__input input {
    border: none;
    width: 45px;
    font-family: "Poppins", sans-serif;
    font-size: 14px;
    line-height: 18px;
    color: #1d1613;
    cursor: pointer; }
    .price__input input:hover {
      color: rgba(29, 22, 19, 0.7); }

.price__input--min {
  left: 36px; }

.price__input--max {
  left: 140px; }
